.landing--features {
	padding: 60px 0 0 0;
	min-height: 100vh;
}

.landing--features--container {
	width: 80%;
	margin: 0 auto;
}

.landing--features--person--container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

.landing--features--grey--background {
	background-color: #ededed;
	min-height: 100vh;
	-webkit-clip-path: polygon(0 0, 100% 25vh, 100% 80%, 0 100%);
	        clip-path: polygon(0 0, 100% 25vh, 100% 80%, 0 100%);
}



