.backer--card {
	background-color: #fff;
	margin: 15px;
	color: #585858;
	-ms-flex-preferred-size: 30%;
	    flex-basis: 30%;
	border-radius: 3px;
	-webkit-box-shadow: 2px 2px 5px rgba(187, 184, 184, 0.4);
	        box-shadow: 2px 2px 5px rgba(187, 184, 184, 0.4);
}

.backer--card header {
	padding: 30px 0;
	border-bottom: 1px solid #ededed;
	width: 100%;
}

.backer--card header img {
	width: 200px;
	margin: 0 auto;
	display: block;
}

.backer--card--body h3 {
	margin: 15px 0;
}

.backer--card--container {
	width: 80%;
	margin: 0 auto;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
}



