.header {
  height: 95vh;
  position: relative;
  background: -webkit-gradient(linear, left top, right bottom, from(rgba(21, 87, 153, 1)), to(rgba(32, 151, 243, 1)));
  background: -webkit-linear-gradient(left top, rgba(21, 87, 153, 1), rgba(32, 151, 243, 1));
  background: -o-linear-gradient(left top, rgba(21, 87, 153, 1), rgba(32, 151, 243, 1));
  background: linear-gradient(to right bottom, rgba(21, 87, 153, 1), rgba(32, 151, 243, 1));

  -webkit-clip-path: polygon(0 0, 100% 0, 100% 75vh, 0 100%);

          clip-path: polygon(0 0, 100% 0, 100% 75vh, 0 100%);
}

.header--logo {
  position: absolute;
  top: 15%;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}

.header--banner {
  position: absolute;
  top: 30%;
  left: 50%;
  text-align: center;
  color: #fff;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}

.header--banner h1 {
  font-size: 3em;
  letter-spacing: 3px;
  margin-bottom: 5px;
  -webkit-transition: opacity 0.5s ease;
  -o-transition: opacity 0.5s ease;
  transition: opacity 0.5s ease;
  -webkit-animation: moveInRight 0.5s ease-in;
          animation: moveInRight 0.5s ease-in;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.header--banner h2 {
  font-size: 1.5em;
  letter-spacing: 3px;
  -webkit-transition: opacity 0.5s ease;
  -o-transition: opacity 0.5s ease;
  transition: opacity 0.5s ease;
  -webkit-animation: moveInLeft 0.5s ease-in;
          animation: moveInLeft 0.5s ease-in;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.header--button {
  padding: 15px;
  width: 200px;
  border-radius: 100px;
  margin-top: 50px;
  color: #185da2;
  font-size: 1em;
  border: none;
  background-color: #fff;
  cursor: pointer;
  -webkit-box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.4);
          box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.4);
  -webkit-transition: opacity 0.5s ease;
  -o-transition: opacity 0.5s ease;
  transition: opacity 0.5s ease;
  -webkit-animation: moveInBottom 0.4s ease-in;
          animation: moveInBottom 0.4s ease-in;
}

.header--button:hover {
  -webkit-box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.4);
          box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.4);
}



