.landing--whitepaper {
	background: -webkit-gradient(linear, left top, right bottom, from(rgba(21, 87, 153, 1)), to(rgba(32, 151, 243, 1)));
	background: -webkit-linear-gradient(left top, rgba(21, 87, 153, 1), rgba(32, 151, 243, 1));
	background: -o-linear-gradient(left top, rgba(21, 87, 153, 1), rgba(32, 151, 243, 1));
	background: linear-gradient(to right bottom, rgba(21, 87, 153, 1), rgba(32, 151, 243, 1));
	color: #fff;
	-webkit-clip-path: polygon(0 0, 100% 20%, 100% 100%, 0 100%);
	        clip-path: polygon(0 0, 100% 20%, 100% 100%, 0 100%);
}

.landing--whitepaper > h1 {
	text-align: center;
	padding-top: 150px;
}

.landing--whitepaper--container {
	height: 100%;
	padding: 200px 0;
	width: 80%;
	margin: 0 auto;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}




